}
div#Projects > h2 {
    text-align: center;
}


div#Projects > div#projects-grid {
    /* The projects should start with a default of one column */
    display: grid;
    grid-template-columns: 1fr;
    grid-auto-flow: row;
    grid-gap: 0.25em;
    max-width: 100%
}
/*
    There is more room on mobile phones in landscape mode.
    lets program the grid to take advantage of the extra space
    
    48ems min-width targets tablets with the current font scheme
*/

